import { commonData, cardDataRu, cardDataEn } from "./data";
import { showCards } from './cardsLogic';

const ruBtn = document.getElementById('lang-switch__RU__id');
const enBtn = document.getElementById('lang-switch__EN__id');
let language = localStorage.getItem('currentLang');

ruBtn.addEventListener('click', (target) => {
	makeTranslation(target);
});

enBtn.addEventListener('click', (target) => {
	makeTranslation(target);
});

function makeTranslation(target){
	target.target.focus();

	if (target.target.id === 'lang-switch__EN__id'){
		language = 'en';
		localStorage.setItem('currentLang', language);

	} else if(target.target.id === 'lang-switch__RU__id'){
		language = 'ru';
		localStorage.setItem('currentLang', language);
	};

	formHeader(commonData);	
	formEducation(commonData);
	formSkills(commonData);
	formProjects();
};

function formHeader(commondata){
	const educationTitle = document.getElementById('nav-wrapper__education');
	const skillsTitle = document.getElementById('nav-wrapper__skills');
	const projectTitle = document.getElementById('nav-wrapper__projects');
	const nameTitle = document.getElementById('personal-info__first-name');
	const surnameTitle = document.getElementById('personal-info__surname');
	
	if (language == 'en'){
		let dataArr = Object.values(commondata.header);

		educationTitle.textContent = dataArr[0];
		skillsTitle.textContent = dataArr[1];
		projectTitle.textContent = dataArr[2];
		nameTitle.textContent = dataArr[3];
		surnameTitle.textContent = dataArr[4];

	} else if (language == 'ru'){
		let dataArr = Object.keys(commondata.header);
		
		educationTitle.textContent = dataArr[0];
		skillsTitle.textContent = dataArr[1];
		projectTitle.textContent = dataArr[2];
		nameTitle.textContent = dataArr[3];
		surnameTitle.textContent = dataArr[4];
	}
}

function formEducation(commondata){
	const header = document.getElementById('info-block__header-edu__id');
	const univerName = document.getElementById('info-block-edu__list-title__id');

	if (language == 'en'){
		let dataArr = Object.keys(commondata.education);
		
		header.textContent = dataArr[0];
		univerName.textContent = dataArr[1];

	} else if (language == 'ru'){
		let dataArr = Object.values(commondata.education);
		
		header.textContent = dataArr[0];
		univerName.textContent = dataArr[1];
	}
}

function formSkills(commondata){
	const header = document.getElementById('info-block__header-skills__id');
	const headerMain = document.getElementById('info-block-main__list-title__id');
	const liMain = document.getElementById('info-block-main__li__id');
	const headerAdd = document.getElementById('info-block-addit__list-title__id');

	if (language == 'en'){
		let dataArr = Object.keys(commondata.skills);
		
		header.textContent = dataArr[0];
		headerMain.textContent = dataArr[1];
		headerAdd.textContent = dataArr[2];
		liMain.textContent = `${dataArr[3]} (C1 / Advanced)`;

	} else if (language == 'ru'){
		let dataArr = Object.values(commondata.skills);
		
		header.textContent = dataArr[0];
		headerMain.textContent = dataArr[1];
		headerAdd.textContent = dataArr[2];
		liMain.textContent = dataArr[3];
	}
}

function formProjects(){
	const header = document.getElementById('info-block__header-proj__id');

	if (language == 'en'){		
		header.textContent = 'PROJECTS';
		
	} else if (language == 'ru'){
		header.textContent = 'ПРОЕКТЫ';
	}

	showCards(cardDataRu, cardDataEn);
}
