import '../../../styles/index.scss';

//Переключение блоков
function addListeners(){
	const menuItems = Array.from(document.getElementsByClassName('nav-menu-item'));

	menuItems.forEach((element)=>{
		element.addEventListener('click', (target)=>{
			target.target.focus();
			const currentBlock = target.target.innerText;
			displayOrHideBlocks(currentBlock);
		});
	});
}

addListeners();

function displayOrHideBlocks(whatBlock){
	const education = document.getElementsByClassName('education-block')[0];
	const skills = document.getElementsByClassName('skills-block')[0];
	const portfolio = document.getElementsByClassName('portfolio-block')[0];

	if (whatBlock == 'ОБРАЗОВАНИЕ' || whatBlock == 'EDUCATION'){
		education.classList.remove(('invisible'));
		skills.classList.add(('invisible'));
		portfolio.classList.add(('invisible'));
	} else if (whatBlock == 'НАВЫКИ' || whatBlock == 'SKILLS'){
		skills.classList.remove(('invisible'));
		education.classList.add(('invisible'));
		portfolio.classList.add(('invisible'));
	} else {
		portfolio.classList.remove(('invisible'));
		skills.classList.add(('invisible'));
		education.classList.add(('invisible'));
	}
}
