import { cardDataRu, cardDataEn } from './data';

const wrapper = document.getElementById('cards-wrapper');

document.getElementById('nav-wrapper__projects').addEventListener('click', () => {
	showCards(cardDataRu, cardDataEn);
});

export function showCards(dataArr1, dataArr2){
	wrapper.innerHTML = null;
	let language = localStorage.getItem('currentLang');

	if (language == 'ru'){
		dataArr1.forEach((element) => {
			drawCards(element);
		});
	} else if(language == 'en'){
		dataArr2.forEach((element) => {
			drawCards(element);
		});
	}
}

function drawCards(element){
	const card = document.createElement('div');
	card.setAttribute('class', 'portfolio-block-card');
	wrapper.appendChild(card);

	const cardImg = document.createElement('img');
	cardImg.setAttribute('class', 'portfolio-block-card__img');
	cardImg.setAttribute('src', `${element.imgSrc}`);
	cardImg.setAttribute('alt', `${element.imgAlt}`);
	cardImg.setAttribute('width', '500');
	cardImg.setAttribute('height', '300');
	card.appendChild(cardImg);

	const cardInfo = document.createElement('div');
	cardInfo.setAttribute('class', 'portfolio-block-card__info');
	card.appendChild(cardInfo);

	const cardInfoMain = document.createElement('div');
	cardInfoMain.setAttribute('class', 'card__info-main');
	cardInfo.appendChild(cardInfoMain);

	const infoMainTags = document.createElement('div');
	infoMainTags.setAttribute('class', 'card__info-tags');
	cardInfoMain.appendChild(infoMainTags);

	element.tags.forEach((tag) => {
		const thetag = document.createElement('p');
		thetag.setAttribute('class', 'card__info-tag');
		thetag.innerHTML = tag;
		infoMainTags.appendChild(thetag);
	});

	const link = document.createElement('a');
	link.setAttribute('href', `${element.link}`);
	cardInfoMain.appendChild(link);

	const imgWrap = document.createElement('div');
	imgWrap.setAttribute('class', 'github-icon');
	link.appendChild(imgWrap);

	const imgIcon = document.createElement('img');
	imgIcon.setAttribute('class', 'social-icon');
	imgIcon.setAttribute('alt', 'Логотип Гитхаб');
	imgIcon.setAttribute('src', 'icons/github.png');
	imgIcon.setAttribute('width', '40');
	imgIcon.setAttribute('height', '40');
	imgWrap.appendChild(imgIcon);

	const cardInfoAdditional = document.createElement('div');
	cardInfoAdditional.setAttribute('class', 'card__info-additional');
	cardInfo.appendChild(cardInfoAdditional);

	const date = document.createElement('p');
	date.setAttribute('class', 'card__info-date');
	date.innerHTML = element.date;
	cardInfoAdditional.appendChild(date);

	const description = document.createElement('div');
	description.setAttribute('class', 'card__info-description');
	description.innerHTML = element.description;
	cardInfoAdditional.appendChild(description);
};

showCards(cardDataRu, cardDataEn);
